
.tem-product {
  width: 1000px;
  margin: 0 auto;
}
.tem-product .kind-title {
  background-color: pink;
  height: 54px;
}
.tem-product .kind-title span:nth-child(1) {
  font-size: 22px;
  margin-left: 24px;
  line-height: 54px;
  float: left;
}
.tem-product .kind-title span:nth-child(2) {
  font-size: 16px;
  margin-right: 24px;
  line-height: 54px;
  float: right;
  cursor: pointer;
}
.tem-product ul {
  margin: 30px auto 0;
}
.tem-product ul li {
  position: relative;
  float: left;
  width: 25%;
  height: 350px;
  background-color: #ccc;
}
.tem-product ul li .hot {
  height: 20px;
  width: 40px;
  background-color: red;
  color: white;
  position: absolute;
}
.tem-product ul li img {
  margin: 30px auto;
}
.tem-product ul li .pro-type {
  font-size: 18px;
}
.tem-product ul li .pro-type span:nth-child(1) {
  color: black;
}
.tem-product ul li .pro-type span:nth-child(2) {
  color: red;
}
.tem-product ul li .pro-name {
  font-size: 16px;
  color: #8f8f8f;
}



