
.sf-footer {
  width: 100%;
  background-color: #2e2e2e;
  padding-top: 40px ;
  color: white;
}
.sf-footer .footer-info {
  width: 1200px;
}
.sf-footer .footer-info .footer-ul li {
  float: left;
  width: 20%;
}
.sf-footer .footer-info .footer-ul li .erweima {
  color: #666;
  height: 35px;
  line-height: 35px;
  font-size: 12px;
}
.sf-footer .footer-info .footer-ul li img {
  margin: 0 auto;
}
.sf-footer .footer-info .footer-ul li dt a {
  color: #888;
  height: 35px;
  line-height: 35px;
  font-size: 14px;
}
.sf-footer .footer-info .footer-ul li dd a {
  color: #666;
  height: 35px;
  line-height: 35px;
  font-size: 12px;
}
.sf-footer .location {
  background-color: #232323;
}
.sf-footer .location li {
  width: 33.33%;
  height: 53px;
  line-height: 53px;
  float: left;
}
.sf-footer .location li i,
.sf-footer .location li span,
.sf-footer .location li a {
  vertical-align: center;
  line-height: 53px;
  color: #838383;
  font-size: 14px;
}
.sf-footer .copyright {
  height: 53px;
  width: 100%;
  line-height: 53px;
}
.sf-footer .copyright li {
  color: #acafb1;
  float: left;
  width: 50%;
}
.sf-footer .copyright li a,
.sf-footer .copyright li p,
.sf-footer .copyright li span {
  font-size: 14px;
  line-height: 53px;
  color: #acafb1;
}



