
.sf-index {
  text-align: center;
}
.sf-index .our-server h2 {
  font-size: 44px;
  height: 90px;
  line-height: 90px;
}
.sf-index .our-server p {
  font-size: 16px;
  line-height: 30px;
  height: 30px;
}
.sf-index .server-all {
  margin-top: 40px;
}
.sf-index .server-all li {
  float: left;
  width: 20%;
}
.sf-index .server-all li .server-icon img {
  margin: 0 auto;
}
.sf-index .server-all li h4 {
  height: 80px;
  line-height: 80px;
  font-size: 20px;
  font-weight: 400px;
  color: black;
}
.sf-index .server-all li .blue-border {
  margin-bottom: 15px;
}
.sf-index .server-all li span {
  font-size: 16px;
  color: #6d7373;
}
.sf-index .our-product {
  margin-top: 40px;
}
.sf-index .sf-about {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 100px;
  margin: 80px auto;
}
.sf-index .sf-about li {
  width: 420px;
}
.sf-index .sf-about li img {
  margin: 0 auto;
}
.sf-index .sf-about li dt {
  margin: 27px 0;
  height: 50px;
  line-height: 50px;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
.sf-index .sf-about li dt span:nth-child(1) {
  float: left;
}
.sf-index .sf-about li dt span:nth-child(2) {
  float: left;
}
.sf-index .sf-about li dt span:nth-child(3) {
  float: right;
}
.sf-index .sf-about li dd span {
  width: 80%;
  text-align: left;
  display: inline-block;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
}
.sf-index .sf-about li dd i {
  width: 20%;
  text-align: right;
  display: inline-block;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
}
.sf-index .sf-about li:nth-child(1) {
  float: left;
}
.sf-index .sf-about li:nth-child(2) {
  float: right;
}
.blue-border {
  height: 4px;
  width: 45px;
  background-color: blue;
  margin: 0 auto;
}
.server-title {
  margin-bottom: 50px;
}
.server-title h2 {
  font-size: 44px;
  height: 90px;
  line-height: 90px;
}
.server-title p {
  font-size: 16px;
  line-height: 30px;
  height: 30px;
}
.division {
  width: 100%;
  height: 40px;
}



