
.partner-wrapper {
  overflow: hidden;
  margin: 0 auto;
  width: 800px;
  height: 400px;
}
.partner-slider {
  width: 600%;
  height: 400px;
  background-color: pink;
  margin: 0 auto;
  position: relative;
}
.partner-slider li {
  float: left;
  position: absolute;
}
.partner-slider li img {
  width: 800px;
  height: 400px;
}
.partner-slider li .partner-name {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}
.partner-slider li .partner-name p:nth-child(1) {
  font-size: 16px;
  font-weight: bold;
  height: 40px;
  line-height: 40px;
}
.partner-slider li .partner-name p:nth-child(2) {
  font-size: 14px;
  height: 30px;
  line-height: 30px;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: 1s all ease;
  transition: 1s all ease;
}



